<template>
  <div>
    <div class="root">
      <div class="box">
        <div class="herb-title"><h2>增加方剂</h2></div>
        <div class="topItem">
          <div class="topItem-left">
            <el-form ref="form" :model="form" label-width="80px" :rules="pubFormRules">
              <el-form-item label="药材名称" class="change-label-calss" prop="herbname">
                <el-input v-model="form.herbname" placeholder="请书写草药名称"></el-input>
              </el-form-item>
              <el-form-item label="药材类别" class="change-label-calss" prop="herbtype">
                <el-input v-model="form.herbtype" placeholder="请书写草药类别"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <div class="topItem-right">
            <el-form ref="form" :model="form" label-width="80px" :rules="pubFormRules">
              <el-form-item label="药材产地" class="change-label-calss" prop="city">
                <el-input v-model="form.city" placeholder="请书写药材产地"></el-input>
              </el-form-item>
              <el-form-item label="产地数据" class="change-label-calss" prop="value">
                <el-input v-model="form.value" placeholder="请书写产地数据"></el-input>
              </el-form-item>
            </el-form>
          </div>
          <div class="topItem-right-r">
            <div class="topItem-limit">
              <h3>上传方剂注意事项</h3>
              <p>药材产地填写格式：xx,xx,xx "xx"为城市名称，产地数据填写格式同理，"xx"为六位数数值,该两项为非必填项。</p>
            </div>
          </div>

        </div>

        <div class="bottomItem">
          <el-form ref="form" :model="form" label-width="80px" size="medium">
            <quill-editor v-model="form.content"></quill-editor>
          </el-form>
        </div>

        <div class="submitbtn">
            <el-row>
                <el-button @click="resetFn()">清空</el-button>
                <el-button type="primary" @click="pubArticleFn()">提交</el-button>
                <el-button type="info" @click="pubArticleFn('草稿')">保存草稿</el-button>
            </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { addHerbInfo } from '@/api'
export default {
    data(){
        return{
          form:{
            herbname:'',
            herbtype:'',
            content:'',
            city:'',
            value:''
          }
        }
    },
    mounted(){

    },
    methods:{
      async pubArticleFn(){
        if(this.form.content == ''){
            this.$message({
                type: 'warning',
                message: '草药正文内容不能为空！'
            })
        }else if(this.form.herbname == ''){
          this.$message({
              type: 'warning',
              message: '草药名称不能为空！'
          })
        }else if(this.form.herbtype == ''){
          this.$message({
              type: 'warning',
              message: '草药类别不能为空！'
          })
        }else{
            const { data: res } = await addHerbInfo(this.form)
            if(res.status !== 0) return this.$message.error(res.message)
            this.$message.success(res.message)
        }
      }
    }
}
</script>

<style scoped>
.root{
    height: 1900px;
    width: 100%;
    padding-top: 100px;
    background: url('@/assets/中草药滚动背景.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-size: 140% 100%;
    animation: gradientBG 60s ease infinite;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    flex-direction: column;
    -webkit-display:flex;
    display:flex;
    overflow: hidden;
}
@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.box{
  /* background: #c7c7c7af; */
  width: 75%;
  min-height: 500px;
  border-radius: 20px;
  margin: 0 auto;
  margin-top: 7vh;
  padding: 20px;
  border: 1px #e0d8d0 solid ;
}
::v-deep .el-form-item__label {
  color: #e0d8d0!important;
  font-size: 15px;
  font-weight: 600;
}
::v-deep .el-input {
  /* width: 70%; */
}
.herb-title{
  display: block;
  padding: 20px;
  margin: 0 auto;
  text-align: center;
  /* font-family: 'xingkai'; */
  font-size: 17px;
  color: #f5f1ed;
}
.submitbtn{
width: 100%;
}
.submitbtn .el-button{
float: right;
margin-right: 20px;
}
::v-deep .ql-editor{
    min-height: 300px;
    background-color: #ddd;
}
.topItem{
  display: flex;
  justify-content: space-around;
}
.topItem-left{
  width: 35%;
}
.topItem-right{
  width: 35%;
}
.topItem-right-r{
  width: 20%;
}
.topItem-limit{
  padding: 10px;
  border: 1px solid #f5f1ed;
  border-radius: 5px;
  color: #f5f1ed;
}
.topItem-limit h3{
  font-size: 15px;
  text-align: center;
}
.topItem-limit p{
  font-size: 12px;
  color: #d8d8d8;
}
@media (min-width: 1699px) {
  .box{
    width: 75%;
    min-height: 840px;
    margin-top: 7vh;
    padding: 20px;
  }
  .bottomItem{
    height: 550px;
  }
  .quill-editor{
    height: 550px;
  }
}
</style>